/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
	font-family: "Muli", "Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
	font-weight: 800;
	text-transform: uppercase;
	color: #be1e2d;
	border: 2px solid #be1e2d;
	transition: all 0.5s ease-in-out;
	font-size: 12px;
	/*font-size: $sizeValue * 1rem;*/
	line-height: 1;
	letter-spacing: .1em;
	padding: .6em 1em;
	background: transparent;
}

button:link,
input[type="button"]:link,
input[type="reset"]:link,
input[type="submit"]:link {
	background-color: transparent;
	border-color: #be1e2d;
	color: #be1e2d;
}

button:hover,
input[type="button"]:hover,
input[type="reset"]:hover,
input[type="submit"]:hover {
	background-color: transparent;
	border-color: #be1e2d;
	color: #be1e2d;
	fill: #be1e2d;
	text-decoration: none;
}

button:hover svg,
input[type="button"]:hover svg,
input[type="reset"]:hover svg,
input[type="submit"]:hover svg {
	fill: #be1e2d;
	left: 5px;
}

button:active,
input[type="button"]:active,
input[type="reset"]:active,
input[type="submit"]:active {
	background-color: transparent;
	border-color: #be1e2d;
	color: #be1e2d;
	box-shadow: none;
}

button:active svg,
input[type="button"]:active svg,
input[type="reset"]:active svg,
input[type="submit"]:active svg {
	fill: #be1e2d;
	left: 5px;
}

button:focus, button:active:focus,
input[type="button"]:focus,
input[type="button"]:active:focus,
input[type="reset"]:focus,
input[type="reset"]:active:focus,
input[type="submit"]:focus,
input[type="submit"]:active:focus {
	background-color: transparent;
	border-color: #be1e2d;
	color: #be1e2d;
	outline: 5px auto #be1e2d;
}

button:focus, button:active:focus svg,
input[type="button"]:focus,
input[type="button"]:active:focus svg,
input[type="reset"]:focus,
input[type="reset"]:active:focus svg,
input[type="submit"]:focus,
input[type="submit"]:active:focus svg {
	fill: #be1e2d;
	left: 5px;
}

button:visited,
input[type="button"]:visited,
input[type="reset"]:visited,
input[type="submit"]:visited {
	background-color: transparent;
	border-color: #be1e2d;
}

button:hover,
input[type="button"]:hover,
input[type="reset"]:hover,
input[type="submit"]:hover {
	border-color: #8f1622;
	color: #8f1622;
}

button:active, button:focus,
input[type="button"]:active,
input[type="button"]:focus,
input[type="reset"]:active,
input[type="reset"]:focus,
input[type="submit"]:active,
input[type="submit"]:focus {
	border-color: #8f1622;
	color: #8f1622;
}

