/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Links
--------------------------------------------------------------*/
a {
	color: #be1e2d;
	transition: all 0.5s ease-in-out;
}

a:visited {
	color: #8f1622;
}

a:hover, a:focus, a:active {
	color: #8f1622;
	text-decoration: none;
}

a:focus {
	outline: thin dotted;
}

a:hover, a:active {
	outline: 0;
}

/*--------------------------------------------------------------
## Menus
--------------------------------------------------------------*/
.main-navigation {
	clear: both;
	display: block;
	float: left;
	width: 100%;
}

.main-navigation ul {
	display: none;
	list-style: none;
	margin: 0;
	padding-left: 0;
}

.main-navigation ul ul {
	box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
	float: left;
	position: absolute;
	top: 100%;
	left: -999em;
	z-index: 99999;
	opacity: 0;
	transition: opacity 0.5s ease-in-out;
}

.main-navigation ul ul ul {
	left: -999em;
	top: 0;
}

.main-navigation ul ul li:hover > ul,
.main-navigation ul ul li.focus > ul {
	left: 100%;
}

.main-navigation ul ul a {
	width: 200px;
}

.main-navigation ul ul :hover > a,
.main-navigation ul ul .focus > a {
	transition: all 0.5s ease-in-out;
}

.main-navigation ul ul a:hover,
.main-navigation ul ul a.focus {
	background-color: trans(#d2c7e2, 0.95);
	background-color: rgba(210, 199, 226, 0.95);
	color: black;
}

.main-navigation ul li:hover > ul,
.main-navigation ul li.focus > ul {
	left: auto;
	opacity: 1;
}

.main-navigation li {
	float: left;
	position: relative;
}

.main-navigation li:hover > a,
.main-navigation li.focus > a {
	color: white;
	background-color: trans(#333333, 0.95);
	background-color: rgba(51, 51, 51, 0.95);
}

.main-navigation a {
	display: block;
	font-weight: 700;
	text-decoration: none;
	padding: .5em 1em .5em 0;
	color: #d2c7e2;
	line-height: 1.4em;
}

.main-navigation .current_page_item > a,
.main-navigation .current-menu-item > a,
.main-navigation .current_page_ancestor > a,
.main-navigation .current-menu-ancestor > a {
	background-color: trans(#333333, 0.95);
	background-color: rgba(51, 51, 51, 0.95);
}

/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
	display: block;
	float: right;
	/*position: absolute;
	right: 1em;
	top: 1em;*/
}

.menu-toggle {
	font-family: "Muli", "Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
	font-weight: 800;
	text-transform: uppercase;
	color: #d2c7e2;
	border: 2px solid #d2c7e2;
	transition: all 0.5s ease-in-out;
}

.menu-toggle:link {
	background-color: transparent;
	border-color: #d2c7e2;
	color: #d2c7e2;
}

.menu-toggle:hover {
	background-color: transparent;
	border-color: #d2c7e2;
	color: #d2c7e2;
	fill: #d2c7e2;
	text-decoration: none;
}

.menu-toggle:hover svg {
	fill: #d2c7e2;
	left: 5px;
}

.menu-toggle:active {
	background-color: transparent;
	border-color: #d2c7e2;
	color: #d2c7e2;
	box-shadow: none;
}

.menu-toggle:active svg {
	fill: #d2c7e2;
	left: 5px;
}

.menu-toggle:focus, .menu-toggle:active:focus {
	background-color: transparent;
	border-color: #d2c7e2;
	color: #d2c7e2;
	outline: 5px auto #d2c7e2;
}

.menu-toggle:focus, .menu-toggle:active:focus svg {
	fill: #d2c7e2;
	left: 5px;
}

.menu-toggle:visited {
	background-color: transparent;
	border-color: #d2c7e2;
}

.menu-toggle:hover {
	border-color: white;
	color: white;
}

.menu-toggle:active, .menu-toggle:focus {
	border-color: white;
	color: white;
	outline: none;
}

@media screen and (min-width: 900px) {
	.menu-toggle {
		display: none;
	}
	.main-navigation .menu-item-has-children > a,
	.main-navigation .page_item_has_children > a {
		/*	padding-right: 2em;
			background: $color__dark-gray;*/
	}
	.main-navigation ul {
		display: block;
		display: flex;
		flex-wrap: wrap;
	}
