		width: 100%;
		background: transparent;
	}
	.main-navigation ul ul {
		display: block;
		width: 100%;
		clear: both;
		box-shadow: none;
		float: left;
		position: absolute;
		top: 100%;
		left: 0;
		z-index: 99999;
		opacity: 1;
		transition: none;
	}
	.main-navigation ul ul li {
		display: block;
		clear: both;
		padding: 0 1em;
		width: 250px;
	}
	.main-navigation ul ul li ul, .main-navigation ul ul li:hover, .main-navigation ul ul li:focus {
		left: 0;
		opacity: 1;
		transition: none;
	}
	.main-navigation ul ul li:hover > ul {
		left: 0;
		opacity: 1;
		transition: none;
	}
	.main-navigation ul li {
		display: block;
		clear: both;
		padding: 0 1em;
	}
	.main-navigation ul ul {
		display: block;
		position: relative;
		clear: both;
